import { html } from 'lit';
import { property } from 'lit/decorators.js';
import { OmniElement } from '../../omni-element.js';

import {css} from 'lit';
const styles = css`:host{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%}svg{width:6.7857142857rem;animation:loading-spinner .8196721311s linear infinite}slot{font-family:"Open Sans",sans-serif;font-size:9pt;color:#252a37}@keyframes loading-spinner{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}`


/**
 * Spinning loading indicator with optional informative text
 *
 * To include within your project `import { OmniLoadingIndicatorElement } from 'omni-ui';`
 *
 * @tagname omni-loading-indicator
 * @slot - default content placed under the loading indicator
 */
export class OmniLoadingIndicatorElement extends OmniElement {
  static get styles() {
    return [...super.styles, styles];
  }

  @property({ type: String }) color: string = '#10a1d2';
  @property({ type: String }) altText: string = 'Loading spinner graphic';

  render() {
    return html`
      <omni-style>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          style="margin:auto;background:0 0"
          viewBox="0 0 100 100"
          preserveAspectRatio="xMidYMid"
          display="block"
          part="svg">
          <title>${this.altText}</title>
          <circle
            cx="50"
            cy="50"
            r="32"
            stroke-width="6"
            stroke="${this.color}"
            stroke-dasharray="50.26548245743669 50.26548245743669"
            fill="none"
            stroke-linecap="round"
            transform="rotate(128.654 50 50)"></circle>
        </svg>
        <slot part="info"></slot>
      </omni-style>
    `;
  }
}

OmniElement.register('omni-loading-indicator', OmniLoadingIndicatorElement);

declare global {
  interface HTMLElementTagNameMap {
    'omni-loading-indicator': OmniLoadingIndicatorElement;
  }
}
